:root{
  --font:'Dancing Script', cursive;
  --font-name:'Ephesis', cursive;
}

*{
  font-family: 'Dancing Script', cursive;
  font-family: var(--font);
  font-size: 24px;
  color: #fff;
}
a{
  text-decoration: none;
  color: #000;
}
li{
  list-style: none;
}
ul{
  padding-left: 0;
  margin: 0;
}
img{
  width: 100%;
  height: 100%;
}
input,textarea{
  outline: none;
}
p{
  margin: 5px;
}
.App {
 width: 100vw;
 height: 100vh;
 background-size: cover;
 background-repeat: no-repeat;
 position: relative;
}

.App::before{
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}
h4{
  font-family: 'Ephesis', cursive;
  font-family: var(--font-name);
  font-size: 42px;
  font-weight: 400;
}
.App-account{
  position: fixed;
    top: 15px;
    right: 15px;
}
.App-account i{
  font-size: 16px;
  color: #E45826;
  cursor: pointer;
  transition-duration: 0.3s;
  position: relative;
}
.App-account i:hover{
  color: #fff;
}

.App-box-login{
  position: absolute;
  background: rgb(0 0 0 / 40%);
  right: 0;
  top: 35px;
  padding: 10px;
  width: 200px;
  border-radius: 0 0 5px 5px;
  border-bottom: 1px solid red;
}
::-webkit-input-placeholder {
  font-weight: 300;
  font-size: 14px;
  color: rgb(149, 149, 149);
}
::placeholder {
  font-weight: 300;
  font-size: 14px;
  color: rgb(149, 149, 149);
}
.btn-input{
  width: 85%;
  margin-bottom: 25px;
  border: none;
  padding: 10px 15px;
  box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset, rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
  border-radius: 3px;
  color: #787878;
  font-size: 14px;
}
.App-group{
  position: relative;
}
.text-form-error{
  position: absolute;
  bottom:5px;
  right: 0;
  margin: 0;
  font-size: 12px;
  color: #F24C4C;
 
}
.btn-input.is-error{
  border-right: 2px solid rgb(242 76 76);
}
.app-btn{
  border: 1px solid rgb(255, 73 ,73,1);
  background: #ff4949;
  color: #ffffff;
  padding: 10px 20px;
  text-transform: capitalize;
  font-weight: 600;
}
.app-btn:hover{
  border: 1px solid rgb(255 ,73, 73,0.6);
  background: rgb(255 ,73, 73,1);
  color: #fff;
  box-shadow: rgba(255 ,73, 73, 0.2) 5px 5px, rgba(255 ,73, 73, 0.1) 10px 10px;
}
.App-box-login input{

}
/* Slider */
.App-Menu {
  position: fixed;
  right: 5px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 999;
}
.App-Menu ul li{
  background: rgba(255, 255, 255, 0.567);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 10px 0;
  cursor: pointer;
}
.App-Menu ul li.is-active{
  background: #F47C7C;
}

.App-gallery {
    position: relative;
    z-index: 999;
    background: #b245912e;  /* fallback for old browsers */
    background: linear-gradient(to right, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%));
}
.App-gallery .Item-gallery{
    width: 300px;
    margin: 20px auto;
    
}
.carousel__wrapper {
	border-radius: 5px;
	box-shadow: 0 1rem 2rem rgba(purple, .2);
	overflow: hidden;
    border: 2px solid #FF6464;
    background: #f15c5c;
}

.carousel__controls {
	display: flex;
}

.carousel__container {
	display: flex;
	height: 550px;
	position: relative;
	overflow: hidden;
}

.carousel__image {
	position: absolute;
	left: 0;
	opacity: 0;
	top: 0;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	transition: all 1s ease-out;
}
.carousel__image.active {
    opacity: 1;
		-webkit-transform: scale(1);
		        transform: scale(1);
}
.carousel__button {
	background: #f15c5c;
	border: 0;
	color: purple;
	cursor: pointer;
	font-size: 1rem;
	flex-grow: 1;
	outline: none;
	padding: 1rem;
	transition: all .1s ease-out;
    border-radius: 0;
}
.carousel__button:hover{
    background: rgb(241 92 92 / 50%);
}
.flavor-text {
	display: block;
	color: rgba(#fff, .5);
	font-size: .75rem;
	font-weight: 300;
	letter-spacing: 2px;
	line-height: 1.5;
	margin: 1rem auto;
	max-width: 100%;
	text-align: center;
	text-transform: uppercase;
	width: 15rem;
}
.App-home {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.App-info {
  width: 1000px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.App-info h1 {
  font-family: var(--font-name);
  text-transform: capitalize;
  font-size: 30px;
  font-weight: 400;
  padding-top: 20px;
}
.date {
  font-size: 16px;
  text-align: center;
}
.App-info .bg-info {
  position: relative;
  padding: 20px;
  border-radius: 40px;
  border-bottom: 1px solid #f73d93;
  border-right: 1px solid #f73d93;
  transition-duration: 0.5s;
  background: rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  min-width: 280px;
  min-height: 320px;
  transition: 0.6s;
}
.App-info-img {
  width: 150px;
  height: 150px;
  margin: 0 auto;
  transition: 0.6s;
}
.App-info-img img {
  border-radius: 50%;
  border: 6px solid #f47c7c;
}
.App-info-img img.border-me {
  border: 6px solid #646fd4;
}
.App-info .bg-info:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.35);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
.info-content {
  transition: 0.6s;
}
.App-info .bg-info:hover .info-content {
  display: none;
}
