.glitch-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.glitch {
  position: relative;
  margin-top: 40px;
  font-size: 26px;
  font-weight: 500;
  line-height: 1.2;
  color: #fff;
  letter-spacing: 5px;
  -webkit-animation: shift 6s ease-in-out infinite alternate;
          animation: shift 6s ease-in-out infinite alternate;
  -webkit-transform: skewX(0deg);
          transform: skewX(0deg);
  z-index: 1;
}

@-webkit-keyframes shift {
  0%,
  40%,
  44%,
  58%,
  61%,
  65%,
  69%,
  73%,
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }

  41% {
    -webkit-transform: skewX(10deg);
            transform: skewX(10deg);
  }

  42% {
    -webkit-transform: skewX(-10deg);
            transform: skewX(-10deg);
  }

  59% {
    -webkit-transform: skewX(40deg) skewY(10deg);
            transform: skewX(40deg) skewY(10deg);
  }

  60% {
    -webkit-transform: skewX(-40deg) skewY(-10deg);
            transform: skewX(-40deg) skewY(-10deg);
  }

  63% {
    -webkit-transform: skewX(10deg) skewY(-5deg);
            transform: skewX(10deg) skewY(-5deg);
  }

  70% {
    -webkit-transform: skewX(-50deg) skewY(-20deg);
            transform: skewX(-50deg) skewY(-20deg);
  }

  71% {
    -webkit-transform: skewX(10deg) skewY(-10deg);
            transform: skewX(10deg) skewY(-10deg);
  }
}

@keyframes shift {
  0%,
  40%,
  44%,
  58%,
  61%,
  65%,
  69%,
  73%,
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }

  41% {
    -webkit-transform: skewX(10deg);
            transform: skewX(10deg);
  }

  42% {
    -webkit-transform: skewX(-10deg);
            transform: skewX(-10deg);
  }

  59% {
    -webkit-transform: skewX(40deg) skewY(10deg);
            transform: skewX(40deg) skewY(10deg);
  }

  60% {
    -webkit-transform: skewX(-40deg) skewY(-10deg);
            transform: skewX(-40deg) skewY(-10deg);
  }

  63% {
    -webkit-transform: skewX(10deg) skewY(-5deg);
            transform: skewX(10deg) skewY(-5deg);
  }

  70% {
    -webkit-transform: skewX(-50deg) skewY(-20deg);
            transform: skewX(-50deg) skewY(-20deg);
  }

  71% {
    -webkit-transform: skewX(10deg) skewY(-10deg);
            transform: skewX(10deg) skewY(-10deg);
  }
}

:root {
    --white: #fff;
    --active-color: #00aeb0;
  }
input[type="range"] {
    height: 5px;
    -webkit-appearance: none;
    width: 100%;
    margin-bottom: 10px;
    border-radius: 8px;
    background: #3b7677;
    transition: background 0.2s ease;
    cursor: pointer;
  }
  button{
    background: transparent;
    box-shadow: none;
    
  }
  button:hover svg path{
    transition: 0.3s ease;
  }
  .pause svg path{
    fill: #FF8DC7;
  }
  button:hover {
    box-shadow: none;
  }
  button:hover svg path{
    fill: #FFB9B9;
  }
  .audio-player {
    max-width: 350px;
    padding: 24px;
    border-radius: 20px;
    box-shadow: 0 28px 28px rgba(0, 0, 0, 0.2);
    margin: auto;
    color: #fff;
    color: var(--white);
    background: linear-gradient(45deg, #ffacc887 10%, transparent 100%) no-repeat;
    /* background: rgb(255 255 255 / 18%); */
  }
  
  .artwork {
    border-radius: 120px;
    display: block;
    margin: auto;
    height: 200px;
    width: 200px;
  }
  
  .track-info {
    text-align: center;
    z-index: 1;
    position: relative;
  }
  
  .title {
    font-weight: 700;
    margin-bottom: 4px;
  }
  
  .artist {
    font-weight: 300;
    margin-top: 0;
  }
  
  .audio-controls {
    display: flex;
    justify-content: space-between;
    width: 75%;
    margin: 0 auto 15px;
  }
  
  .audio-controls .prev svg,
  .audio-controls .next svg {
    width: 35px;
    height: 35px;
  }
  
  .audio-controls .play svg,
  .audio-controls .pause svg {
    height: 40px;
    width: 40px;
  }
  
  .audio-controls path {
    fill: #fff;
    fill: var(--white);
  }
  .color-backdrop {
    /* background: linear-gradient(45deg, var(--active-color) 20%, transparent 100%) no-repeat; */
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }
  
  .audio-player.playing {
    -webkit-animation: colorChange 20s alternate infinite;
            animation: colorChange 20s alternate infinite;
  }
  
  @-webkit-keyframes colorChange {
    from {
      -webkit-filter: hue-rotate(0deg);
              filter: hue-rotate(0deg);
    }
    to {
      -webkit-filter: hue-rotate(360deg);
              filter: hue-rotate(360deg);
    }
  }
  
  @keyframes colorChange {
    from {
      -webkit-filter: hue-rotate(0deg);
              filter: hue-rotate(0deg);
    }
    to {
      -webkit-filter: hue-rotate(360deg);
              filter: hue-rotate(360deg);
    }
  }
  
